import React from "react"
import "../styles/Login.css"
import {auth, provider} from "../firebase"
import { actionTypes } from "../reducer"
import { useStateValue } from "../StateProvider"

function Login(){
    const [state, dispatch] = useStateValue()

    const signIn = () => {
        auth.signInWithPopup(provider)
            .then(result => {
                dispatch({
                    type: actionTypes.SET_USER,
                    user: result.user
                })
            }).catch(error => alert(error.message))
    }



    return( 

        <div className="login">
             <div className="login__title text-2xl">
                 Hey, welcome to todo list !
            </div>
            <div className="login__logo">
              <img src="https://image.freepik.com/free-vector/vector-illustration-cosmonaut_1441-11.jpg"/>
            </div>
            <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" type="submit" onClick={signIn}>
                Sign in
            </button>
        </div>

)}

export default Login